import Vue from "vue";
import VueRouter from "vue-router";
import Main from '@/views/Main'

import Home from "@/views/Home";
import About from "@/views/About";
import Blog from "@/views/Blog";
import Gallery from "@/views/Gallery";
import FAQ from "@/views/FAQ";
import Contact from "@/views/Contact";

Vue.use(VueRouter);
Vue.use(Home);
Vue.use(About);
Vue.use(Blog);
Vue.use(Gallery);
Vue.use(FAQ);
Vue.use(Contact);
Vue.use(Main);

const router = new VueRouter({
  // mode: 'history',
  routes: [
    {
      path: '/main',
      component: Main,
      children: [
        {
          path: '/Home',
          name: 'Home',
          component: Home
        },{
          path: '/About',
          name: 'About',
          component: About
        },{
          path: '/Blog',
          name: 'Blog',
          component: Blog
        },{
          path: '/Gallery',
          name: 'Gallery',
          component: Gallery
        },{
          path: '/FAQ',
          name: 'FAQ',
          component: FAQ
        },{
          path: '/Contact',
          name: 'Contact',
          component: Contact
        }
      ],
      redirect: '/Home'
    }, {
      path: '/',
      redirect: '/Home'
    }
  ]
});


export default router



